.starProducts{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1vmax auto;
}

.starProducts div:last-child{
    display: flex;
    flex-wrap: wrap;
    width: 40.5%;
}

.starProducts img{
    transition: all 0.5s;
}

.starProducts img:hover{
    box-shadow: 6px 6px 15px rgba(53, 53, 53, 0.36);
}